	module.exports = {"data":{"wordpressPost":{"title":"Top 10 Most Read in March: Caesarean section and for-profit status of hospitals, mental health links to diet, and weight discrimination","content":"<h1>Caesarean sections and for-profit status of hospitals, the link between diet and mental health, and the effect of weight discrimination on physical activity</h1>\n<p><img class=\"alignnone\" title=\"Image: Fruits and Vegetables at Pike Place Market.jpg, by Eric Hunt\" src=\"https://upload.wikimedia.org/wikipedia/commons/thumb/f/f1/Fruits_and_Vegetables_at_Pike_Place_Market.jpg/800px-Fruits_and_Vegetables_at_Pike_Place_Market.jpg\" alt=\"File:Fruits and Vegetables at Pike Place Market.jpg\" width=\"419\" height=\"315\" /></p>\n<p>The Top 10 Most Read articles in March showed great variety in topics and study types. In the top spot this month is a systematic review and meta-analysis by Hoxha <em>et al</em> discussing the association of for-profit status of a hospital and the odds of a woman having a caesarean section. Nguyen <em>et al</em>, at number four, undertake a cross-sectional study to investigate the association between fruit and vegetable consumption and the prevalence of psychological distress in middle-ages and older Australians, finding that there is potential for increased fruit and vegetable consumption in helping to reduce psychological distress. Reaching number seven this month is a study looking at the association between perceived weight discrimination and physical activity, by Jackson and Steptoe of UCL. At number eight this month we see a clinical trial comparing flucloxacillin with clindamycin to flucloxacillin alone for the treatment of limb cellulitis, while at number ten we have a pilot study carried out to inform the design of a future trial to determine the effectiveness and cost-effectiveness of a pharmacist-delivered medicines reconciliation service.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Hoxha <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/2/e013670\">Caesarean sections and for-profit status of hospitals: systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Burt<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/9/e011256\">Understanding negative feedback from South Asian patients: an experimental vignette study </a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Ravnskov<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\">\n<div class=\"highwire-cite-title\"><a href=\"http://bmjopen.bmj.com/content/6/6/e010401\">Lack of an association or an inverse association between low-density-lipoprotein cholesterol and mortality in the elderly: a systematic review</a></div>\n</td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Nguyen <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/3/e014201\">Fruit and vegetable consumption and psychological distress: cross-sectional and longitudinal analyses based on a large Australian sample</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Williams <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/2/e012989\">Childhood academic ability in relation to cigarette, alcohol and cannabis use from adolescence into early adulthood: Longitudinal Study of Young People in England (LSYPE)</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Fenton <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/6/e010438\">Systematic review of the association between dietary acid load, alkaline water and cancer</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Jackson et<em> al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/3/e014592\">Association between perceived weight discrimination and physical activity: a population-based study among English middle-aged and older adults</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Brindle<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/3/e013260\">Adjunctive clindamycin for cellulitis: a clinical trial comparing flucloxacillin with or without clindamycin for the treatment of limb cellulitis</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Abraha<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/3/e012759\">Systematic review of systematic reviews of non-pharmacological interventions to treat behavioural disturbances in older patients with dementia. The SENATOR-OnTop series</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Cadman <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/7/3/e013647\">Pharmacist provided medicines reconciliation within 24 hours of admission and on discharge: a randomised controlled pilot study</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"d5de7209-af46-5287-b188-3fd371cb1806"}}

